import React, {useEffect} from "react";
import {appActions} from "../../../redux/app-reducer";
import {useDispatch} from "react-redux";
import {AppDispatch} from "../../../redux/store-redux";

const GetPageWidth: React.FC = () => {
    console.log("GetPageWidth")

    const {setPageWidth} = appActions

    const dispatch = useDispatch<AppDispatch>();


    const setPageWidthLocal  = () => { //    
        const pageWidth1 = document.documentElement.scrollWidth//    
        setTimeout( () => { //  
            const pageWidth2 = document.documentElement.scrollWidth //     
            if (pageWidth1 === pageWidth2) { //        ,
                dispatch(setPageWidth( pageWidth1 )) //    (    )
            }
        }, 300 ) //      
    }

    useEffect(()=>{
        window.addEventListener('resize', setPageWidthLocal) //    
        return ()=>{ //    
            window.removeEventListener('resize', setPageWidthLocal)//    
        }
    },[])

    return <>
    </>
}
export default GetPageWidth
