import {InferActionsTypes} from "./store-redux";
import {ComThunkTp} from "../components/common/types/commonTypes";
import chatApi, {ChannelStatusType, ChatMessagesType, SubscriberType} from "../components/api/chat-api";
import {Dispatch} from "redux";

const SET_MESSAGE = "myApp/dialog2-reducer/SET_MESSAGE";
const SET_CHANNEL_STATUS = "myApp/dialog2-reducer/SET_CHANNEL_STATUS";

export const chatActions = {

    setMessageAC: (newMessages: Array<ChatMessagesType>) => { //      
        return {type: SET_MESSAGE, newMessages} as const
    },
    setChannelStatus: (channelStatus: ChannelStatusType) => { //      
        return {type: SET_CHANNEL_STATUS, channelStatus} as const
    },
}

type ChatActionsTypes =
    InferActionsTypes<typeof chatActions>


const initialState = {
    messages: [] as Array<ChatMessagesType>,
    channelStatus: "pending" as ChannelStatusType
}

type InitialStateChatType = typeof initialState

const chatReducer = (state: InitialStateChatType = initialState, action: ChatActionsTypes): InitialStateChatType => {
    let stateCopy: InitialStateChatType //      
    switch (action.type) {
        case SET_MESSAGE:
            stateCopy = {
                ...state,
                messages: [...state.messages, ...action.newMessages], //     (   )
            }
            return stateCopy
        case SET_CHANNEL_STATUS:
            stateCopy = {
                ...state,
                channelStatus: action.channelStatus, //    WS
            }
            return stateCopy
        default:
            return state
    }
}

type ThType = ComThunkTp<ChatActionsTypes> // ,          AC

let _newMessagesHandler: SubscriberType<Array<ChatMessagesType>> | null = null
//  ,   null,          

const newMessagesHandleCreator = (dispatch: Dispatch) => { // ,  dispatch     
    if (_newMessagesHandler === null) { //    
        _newMessagesHandler = (newMessages: Array<ChatMessagesType>)=> { //   ,       WS
            dispatch(chatActions.setMessageAC(newMessages)) //       
        }
    }
    return  _newMessagesHandler //   -     
}
let _statusChangedHandler: SubscriberType<ChannelStatusType> | null = null
//  ,   null,           ws

const statusChangedHandlerCreator = (dispatch: Dispatch) => {
    if (_statusChangedHandler === null ) {
        _statusChangedHandler = (channelStatus: ChannelStatusType) => {
            dispatch(chatActions.setChannelStatus(channelStatus))
        }
    }
    return  _statusChangedHandler //   -      
}

export const startMessagesListening = (): ThType => { //    WS,     
    return async (dispatch, getState) => {//
        chatApi.startChannel() //     
        chatApi.subscribe("messages-received", newMessagesHandleCreator(dispatch)) //     dal     
        chatApi.subscribe("status-changed",statusChangedHandlerCreator(dispatch)) //     dal      
    }
}
export const stopMessagesListening = (): ThType => { //    WS,     
    return async (dispatch, getState) => {//
        chatApi.unsubscribe("messages-received", newMessagesHandleCreator(dispatch)) //     dal    
        chatApi.unsubscribe("status-changed",statusChangedHandlerCreator(dispatch)) //     dal     
        chatApi.closeChannel() //     
    }
}
export const sendMessageThCr = (newMessage: string): ThType => { //   
    return async (dispatch, getState) => {
        chatApi.sendMessage(newMessage) //  
    }
}

export default chatReducer

