import store from "../../../redux/store-redux";
import {appActions} from "../../../redux/app-reducer";

const getUnixTime = () => {
    return new Date().getTime() //         Unix
}

export const saveDataToNotify = (messageItem:string, style: string = "Warning" ) => {
    setTimeout(()=>{
        store.dispatch( appActions.setNotify( //     
            [
                ...store.getState().app.notify, //   ,      
                {
                    message: messageItem, //   
                    timeUnix: getUnixTime(), //   (   id)
                    style: style
                }
            ]
        ) )
    },30) //     -  
}

export const getPatch = (locationPathname:string) => {
    const patch = locationPathname //   URL  /profile
        .split( "" ) //     ['/', 'd', 'i', 'a', 'l', 'o', 'g', 's', '/', '2', '8', '8', '3', '1',]
    const tempPath: Array<String> = [] //   
    for (let i: number = 1; i < patch.length; i++) { //    ,    '/'
        if (patch[i] === '/') {
            break //  ,   /
        }
        tempPath.push( patch[i] ) //    
    }
    const UpdatedPatch: string = tempPath.join( "" ) //  

    return UpdatedPatch

}
