export type SubscriberType<T> = (data: T) => void
//   - ,        

export type ChatMessagesType = { //     WS
    message: string,
    photo: string,
    userId: number,
    userName: string
}

export type ChannelStatusType = "pending" | "ready"

let subscribers = {
    "messages-received": [] as Array<SubscriberType<Array<ChatMessagesType>>>, //  ,  
    "status-changed": [] as Array<SubscriberType<ChannelStatusType>>//  ,  
}

type subscribersCallbackType = SubscriberType<ChatMessagesType[]> | SubscriberType<ChannelStatusType>

let ws: WebSocket | null = null //  websocket

const openHandler = () => { //     websocket
    subscribers["status-changed"].forEach( (s: SubscriberType<ChannelStatusType>) => { //     
        s( "ready" ) //         
    } )
}

const messageHandler = (e: MessageEvent) => { //   message
    const newMessages = JSON.parse( e.data ) //        ws
    subscribers["messages-received"].forEach( (s: SubscriberType<Array<ChatMessagesType>>) => { //     
        s( newMessages ) //         
    } )
}

const closeHandler = () => { //    close
    console.log( "The network connection has been lost." );

    subscribers["status-changed"].length > 0 &&
        subscribers["status-changed"].forEach( (s: SubscriberType<ChannelStatusType>) => { //     
            s( "pending" ) //         
        } )

    setTimeout( () => {
        createChannel() //         
    }, 3000 )
}


const closeChannelCommon = () => {//   
    ws?.removeEventListener( 'open', openHandler )//    
    ws?.removeEventListener( 'message', messageHandler )//    
    ws?.removeEventListener( 'close', closeHandler ) //    
    ws?.close() //   
    window.removeEventListener( 'offline', closeHandler ) //     
    console.log( "WS CLOSE" );

}

const createChannel = () => { //    WS
    ws && closeChannelCommon() //    ,    WS  ,     
    ws = new WebSocket( "wss://social-network.samuraijs.com/handlers/ChatHandler.ashx" ) //   WS
    ws?.addEventListener( 'open', openHandler ) //    
    ws?.addEventListener( 'message', messageHandler )//    
    ws?.addEventListener( 'close', closeHandler )//    
    window.addEventListener( 'offline', closeHandler ) //     
}

type subscribeEventType = 'status-changed' | "messages-received"
const chatApi = {// api  chat
    startChannel: () => {//     WS
        createChannel()
    },
    closeChannel: () => { //   ,      
        //subscribers["messages-received"] = [] //   ,   ,     
        closeChannelCommon() //   
    },
    subscribe: (subscribeEvent: subscribeEventType, callback: any) => {//      ( )

        if (subscribeEvent === "messages-received") {
            subscribers["messages-received"].push( callback )

        }
        if (subscribeEvent === "status-changed") {
            subscribers["status-changed"].push( callback )
        }
    },
    unsubscribe: (subscribeEvent: subscribeEventType, callback: any) => { //      ( )
        if (subscribeEvent === "messages-received") {
            subscribers["messages-received"].filter( (s: SubscriberType<Array<ChatMessagesType>>) => s !== callback )
            console.log( "subscribe, status-changed:", subscribers )
        }
        if (subscribeEvent === "status-changed") {
            subscribers["status-changed"].filter( (s: SubscriberType<ChannelStatusType>) => s !== callback )
            console.log( "unsubscribe, status-changed:", subscribers )
        }
    },
    sendMessage: (newMessage: string) => { //      WS
        ws?.send( newMessage )
    }
}

export default chatApi

